import Page from 'components/Page';
import React from 'react';
import { format } from 'date-fns';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
  Alert,
  Table,
} from 'reactstrap';
import PageSpinner from 'components/PageSpinner';
import { findDriverEarning, findDrivers,} from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import conf from "../api/config";
import MUIDataTable from "mui-datatables";
class DriverEarningsPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            driver : 'nn',
            from_date: '',
            to_date: '',
            xtoken:'',
            msg: '',
            error: '',
            downloadFile:true,
            preload:false,
            data: [],
            drivers:[],
        };
        AsyncStorage.getItem(conf.secret)
        .then((results) => {
          if( results !== null )
          {
            let obje_results = JSON.parse(results);
            this.setState({
              ...this.state,
              xtoken : obje_results.user.token,
            });
            findDrivers(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    drivers:response.data,
                });
            }).catch((respError) => {});
          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    componentDidMount() {
    }

    firstUpper = (str) => {
        let string = str.toLowerCase();
        return string.charAt(0).toUpperCase() + string.slice(1);
    }
    format_date_str = (str) => {
      let new_str = str.split('-');
      let day = new_str[2].slice(0, 2);
      return format(new Date(new_str[0], new_str[1], day), 'MM/dd/yyyy');
    }
    handleFromDateInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, from_date: v,});
    }
    handleToDateInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, to_date: v,});
    }
    handleDriverInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, driver: v,});
    }
    handleSaveChanges = () => {
      this.setState({...this.state, preload:true});
    //   console.log('this state', this.state);
    findDriverEarning(this.state.xtoken, this.state, this.state.driver)
      .then((response) => {
        //   console.log('exp response', response);
          if( response.status === 200)
          {
              this.setState({
                  ...this.state,
                  msg: response.message,
                  data: response.data,
                  preload:false,
                  error: '',
              });
              this.forceUpdate();
          }
          else
          {
              this.setState({
                  ...this.state,
                  error: response.message,
                  preload:false,
                  msg: '',
              });
          }
      })
      .catch((error) => {
          this.setState({
              ...this.state,
              error: 'Connection error occured',
              preload:false,
          });
          console.log('error', error);
      });
    }
    render() {
        const columns = [
            {
              name: 'Id',
              options: {
                display: 'excluded'
              }
            },
            'Driver',
            'Truck',
            {
                name: 'Load rate',
                options: {
                    display: true,
                    customBodyRender: (value, tableMeta, updateValue) => (
                        <Label>${value}</Label>
                     )
                }
            },
            {
              name: 'Driver rate type',
              options: {
                display:true,
                customBodyRender: (value, tableMeta, updateValue) => (
                    parseInt(value) === 1 ? (
                    <Label>Percentage</Label>
                    ):(
                        <Label>Fixed amount</Label>
                    )
                )
              }
            },
            {
                name: 'Driver rate',
                options: {
                    display: true,
                    customBodyRender: (value, tableMeta, updateValue) => (
                        <Label>{value}</Label>
                     )
                }
            },
            {
              name: 'Earning',
              options: {
                display: true,
                customBodyRender: (value, tableMeta, updateValue) => (
                   <Label>${value}</Label>
                )
              },
            },
            {
              name: 'Date Posted',
              options: {
                  display: true,
                  customBodyRender: (value, tableMeta, updateValue) => (
                    <p>{this.format_date_str(value)}</p>
                  )
              },
          },
        ];

        const data2 = () => {
          let rtn = [];
          this.state.data.map(item => {
            rtn.push([
              item.id,
              item.dlabel,
              item.tlabel,
              item.rate,
              item.rate_type,
              item.drate,
              item.earning,
              item.created_at,
            ]);
          });
          return rtn;
        }
      
        const options = {
            filter: true,
            selectableRows: 'none',
            selectableRowsOnClick:true,
            filterType: 'dropdown',
            responsive: 'vertical',
            draggableColumns: {
              enabled: true
            },
            rowsPerPage: 5,
            selectToolbarPlacement: 'replace',
            downloadOptions: {
                filename: 'driver-earnings-excel-format.csv',
                separator: ';',
                filterOptions: {
                  useDisplayedColumnsOnly: true,
                  useDisplayedRowsOnly: true,
                }
            },
            onDownload: (buildHead, buildBody, columns, data2) => {
              if (this.state.downloadFile) {
                let val= `${buildHead(columns)}${buildBody(data2)}`.trim();
                return val;
              }
      
              return false;
            },
            onRowSelectionChange: (currentRowsSelected, allRows, rowsSelected) => {
              // console.log('currentRowsSelected', currentRowsSelected);
            },
            onRowsDelete: rowsDeleted => {
              // console.log(rowsDeleted, 'were deleted!');
            },
            onChangePage: numberRows => {
              // console.log(numberRows);
            },
            onSearchChange: searchText => {
              // console.log(searchText);
            },
            onColumnSortChange: (column, direction) => {
              // console.log(column, direction);
            },
            onViewColumnsChange: (column, action) => {
              // console.log(column, action);
            },
            onFilterChange: (column, filters) => {
              // console.log(column, filters);
            },
            onCellClick: (cellIndex, rowIndex) => {
              // console.log(cellIndex, rowIndex);
            },
            onRowClick: (rowData, rowState) => {
              // console.log(rowData, rowState);
            },
        };
      return (
        <Page title="Driver earnings" breadcrumbs={[{ name: 'Earnings', active: true }]}>
          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardHeader>
                    Driver Earnings
                    {this.state.preload && (<PageSpinner/>)}
                </CardHeader>
                {this.state.error.length > 0 && (
                    <Alert color="danger">{this.state.error}</Alert>
                )}
              </Card>
            </Col>
          </Row>

          <Row>
            <Col xl={3} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleSelect">Choose driver*</Label>
                      <Input onChange={this.handleDriverInput} value={this.state.driver} defaultValue={this.state.driver} type="select" name="driver">
                        <option value="nn" disabled={true}>Select driver</option>
                        {
                            this.state.drivers.map((stt, idx) => (
                                <option key={idx} value={stt.id}>{this.firstUpper(stt.fname) + "  " + stt.lname}</option>
                            ))
                        }
                      </Input>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
            <Col xl={3} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleSelect">Date from*</Label>
                      <Input onChange={this.handleFromDateInput}
                        type="date" 
                        value={this.state.from_date}
                        name="fromdate"
                        placeholder=""
                      />
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
            <Col xl={3} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleSelect">Date to*</Label>
                      <Input onChange={this.handleToDateInput}
                        type="date" 
                        value={this.state.to_date}
                        name="todate"
                        placeholder=""
                      />
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
            <Col xl={3} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup check row>
                      <Col sm={{ size: 10, offset: 2 }}>
                        <Button type="button" onClick={this.handleSaveChanges}>Run search</Button>
                      </Col>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
        </Row>

          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardBody>
                    <MUIDataTable title={'Driver earnings'}
                    data={this.state.data.map(item => {
                      return [
                        item.id,
                        item.dlabel,
                        item.tlabel,
                        item.rate,
                        item.rate_type,
                        item.drate,
                        item.earning,
                        item.created_at,
                      ]
                    })} columns={columns} options={options} />
                </CardBody>
              </Card>
            </Col>
          </Row>
    
        </Page>
      );
    }
}
export default DriverEarningsPage;
